import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '../views/Login.vue'
import Register from '../views/Register.vue'
import MainPage from '../components/Navigation.vue'
import Report from '../views/admin/Report.vue'
import Order from '../views/admin/Order.vue'
import Users from '../views/admin/Users.vue'
import ATM from '../views/admin/ATM.vue'
import Mailbox from '../views/admin/Mailbox.vue'
import Dashboard from '../views/admin/Dashboard.vue'
import Addnewatm from '../views/admin/Addnewatm.vue'
import Viewdetail from '../views/admin/Viewdetail.vue'
import Sitting from '../views/admin/Sitting.vue'
import Profile from '../views/admin/Profile.vue'
import Adduser from '../views/admin/AddUser.vue'
import Updateatm from '../views/admin/Updateatm.vue'

import ATMemployee from '../views/employee/ATMemployee.vue'
import DashBoard from '../views/employee/DashBoard.vue'
import OrderEmployee from '../views/employee/OrderEmployee.vue'
import ReportEmployee from '../views/employee/ReportEmployee.vue'
import Employee from '../components/EmployeePage.vue'
import SittingEm from '../views/employee/SittingEm.vue'
import ProfileEm from '../views/employee/ProfileEm.vue'
import Notification from '../views/employee/Notification.vue'
import ViewdetailATM from '../views/employee/ViewdetailATM.vue'



Vue.use(VueRouter)

const routes = [
  {
    path : '/',
    name: 'login',
    component : Login,
    meta: {guestOnly: true }
  },
  {
    path :'/register',
    name : 'register',
    component: Register,
    meta: {guestOnly: true }
  },
  
  {
    path: '/main_page',
    name :'main_page',
    component : MainPage,
    redirect : '/main_page/dashboard',
    children: [
      {
        path : 'dashboard',
        name : 'dashboard',
        component : Dashboard,
        meta: { authOnly: true }
      },
      {
        path : 'report',
        name : 'report_page',
        component : Report
      },
      {
        path : 'mailbox',
        name : 'mailbox',
        component :Mailbox
      },
      {
        path : 'atm',
        name : 'atm_page',
        component : ATM
      },
      {
        path : 'order',
        name : 'order_page',
        component : Order
      },
      {
        path : 'users',
        name : 'users_page',
        component : Users
      },
      {
        path : 'adduser',
        name : 'adduser_page',
        component : Adduser
      },
      {
        path : 'addnewatm',
        name : 'addnewatm_page',
        component : Addnewatm
      },
      {
        path : 'viewdetail/:id',
        name : 'viewdetail_page',
        component : Viewdetail
      },
      {
        path : 'update/:id',
        name : 'update_page',
        component : Updateatm
      },
      {
        path : 'sitting',
        name : 'sitting_page',
        component : Sitting
      },
      {
        path : 'profile',
        name : 'profile_page',
        component : Profile
      },
    ]
  },
    {
      path : '/employee',
      name : 'employee',
      component : Employee,
      redirect: '/employee',
      children : [
        {
          path:'dashboardemployee',
          name: 'dashboardemployee',
          component : DashBoard,
          meta: { authOnly: true }
        },
        {
          path:'atmemployee',
          name: 'atmemployee',
          component : ATMemployee
        },
        {
          path:'orderemployee',
          name: 'orderemployee',
          component :OrderEmployee
        },
        {
          path:'reportemployee',
          name: 'reportemployee',
          component :ReportEmployee
        },
        {
          path : 'sitting',
          name : 'sittingem_page',
          component : SittingEm
        },
        {
          path : 'profile',
          name : 'profileem_page',
          component : ProfileEm
        },
        {
          path : 'notification',
          name : 'notification',
          component :Notification
        },
        {
          path: 'viewdetailatm/:id',
          name : 'viewdetailatm',
          component: ViewdetailATM
        }
 
      ],

    }
]


const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
