              <v-list-item-content>
                <v-list-item-title>
                  <div v-text="item.title"></div>
                </v-list-item-title>
              </v-list-item-content>
            </template>
            <v-list-item
              v-for="child in item.items"
              :key="child.title"
              :to="child.link"
              link
            >
              <v-list-item-icon>
                <v-icon> child.icons" </v-icon>
              </v-list-item-icon>

              <v-list-item-content>
                <v-list-item-title>
                  <div v-text="item.title"></div>
                </v-list-item-title>
              </v-list-item-content>
            </v-list-item>
          </v-list-group>
        </div>
        <div v-else>
          <v-list-item
            v-for="item in navigator.items"
            :key="item.title"
            :to="item.link"
            link
          >
            <v-list-item-icon>
              <v-icon>{{ item.icons }}</v-icon>
            </v-list-item-icon>
            <v-list-item-content>
              <v-list-item-title>
                <div v-text="item.title"></div>
              </v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </div>
