      sample: require("../assets/logo.png"),
    },
    navigation: [
      {
        group: false,
        title: "Dashboard",
        items: [
          {
            title: "Dashboard",
            link: "/employee/dashboardemployee",
            icons: "mdi-view-dashboard",
          },
        ],
      },
      // {
      //       group: false,
      //       title: "Mailbox",
      //       items: [{
      //       title: "Mailbox",
      //       link: "/employee/notification",
      //       icons: "mdi-email-box"
      //       }],
      //     },
      {
        group: false,
        title: "Order Machine",
        items: [
          {
            title: "Order Machine",
            link: "/employee/orderemployee",
            icons: "mdi-text-box-check",
          },
        ],
      },

      {
        group: false,
        title: "Using Machine",
        items: [
          {
            title: "Using Machine",
            link: "/employee/atmemployee",
            icons: "mdi-warehouse",
          },
        ],
      },

      {
        group: false,
        title: "Report",
        items: [
          {
            title: "Report ",
            link: "/employee/reportemployee",
            icons: "mdi-calendar-export",
          },
        ],
      },
    ],
  }),
  created() {
    User.auth().then((response) => {
      this.user = response.data;
      console.log(this.user);
    });
  },
};
</script>
<style lang="scss" scoped>
.list {
  padding: 0px;
  margin: 0px;
  color: #1979e7;
}
.navbar {
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: space-between;
  padding: 0px 30px;
}
</style>
