<template>
  <v-main style="padding: 20px 20px 20px 20px; background-color: #D3D3D3;">
    <v-row style="padding:10px ; height: 90%;">
      <v-card class="rounded-0" width="100%">
        <v-card class="d-flex rounded-0" height="65" style="padding:15px; " outlined>
          <v-card-text style="font-size: 20px;">ORDERS INFORMATION</v-card-text>
          <v-alert
            :value="success"
            type="success">Order Successfully!
          </v-alert>
          <v-alert
            :value="error"
            type="error">Order is not Successfully!
          </v-alert>
          <v-alert
            :value="warning"
            type="warning">Can not delete order.
          </v-alert>
           <v-alert
            :value="info"
            type="info">successfully
          </v-alert>
          <v-dialog
            v-model="dialog"
            max-width="700px">
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                color="#FF8C00"
                dark
                class="mb-2"
                v-bind="attrs"
                v-on="on">Add New Order
              </v-btn>
            </template>
            <v-card>
              <v-card-title style="background-color: #35acd3; text-align:center;" >
                <div class=" white--text"  style="text-align:center;" >{{ formTitle }}</div>
              </v-card-title>
              <v-card-text class="pt-2 pb-0">
                <v-container>
                  <v-row >
                    <v-col
                       cols="12"
                      sm="6"
                      md="6">
                      <v-select
                        :items="itemstypes"
                        v-model="editedItem.atm_type"
                        label="ATM Type"
                        outlined
                      ></v-select>
                    </v-col>
                    <v-col
                      cols="12"
                      sm="6"
                      md="6">
                      <v-select
                        :items="models"
                        v-model="editedItem.atm_model"
                        label="Model"
                        outlined
                        
                      ></v-select>
                    </v-col>
                    <v-col
                      cols="12"
                      sm="6"
                      md="6"
                      >
                      <v-text-field
                        v-model="editedItem.amount"
                        label="Amount"
                        outlined
                      ></v-text-field>
                    </v-col>
                    <v-col
                      cols="12"
                      sm="6"
                      md="6"
                    >
                      <v-select
                      :items="itemscategories"
                        v-model="editedItem.category"
                        label="Category"
                        outlined
                      ></v-select>
                    </v-col>
                  </v-row>
                </v-container>
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn
                    color="red"
                    text
                    @click="close"
                    >Cancel
                </v-btn>
                <v-btn
                  color="blue darken-1"
                  text
                  @click="save(user)"
                >Save
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
          <v-dialog v-model="dialogDelete" max-width="550px">
            <v-card>
              <v-card-title class="text-h5">Are you sure you want to delete this order?</v-card-title>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="red darken-1" text @click="closeDelete">Cancel</v-btn>
                <v-btn color="blue darken-1" text @click="deleteItemConfirm">OK</v-btn>
                <v-spacer></v-spacer>
              </v-card-actions>
            </v-card>
          </v-dialog>         
        </v-card>
        <v-card-container>
          <v-card class="d-flex mb-6"  flat style="height:30px">
            <v-card class="pa-2 mr-auto" flat></v-card>
            <v-card class="pa-2" flat> 
              <v-container style="padding:0; margin:0; margin-top: 10px;margin-right: 10px;  width: 500px;"
                class="d-flex justify-start mb-6" flat tile>
                <v-subheader style="font-size:15px">Search</v-subheader>
                <v-text-field 
                  v-model="search" 
                  outlined
                  dense
                  append-icon="mdi-magnify"
                ></v-text-field>    
              </v-container> 
            </v-card>
          </v-card>
          <v-card style="margin-top:30px; padding:20px; width:100%" flat>
            <v-card >
              <v-data-table
                :items-per-page="itemsPerPage"
                :headers="headers"
                :items="orders"
                :search="search">
                <template v-slot:[`item.created_at`]="{ item }">
                  {{ formatDate(item.created_at) }}
                </template>
                  <template v-slot:[`item.arrival_date`]="{ item }">
                  {{ formatDate(item.arrival_date) }}
                </template>
                   <template v-slot:[`item.status`]="{ item }">
                      <v-chip
                         class="ma-2"
                        color="blue"
                         text-color="white"
                      >
                        {{item.status}}
                      </v-chip>
                  </template>
                <template v-slot:[`item.action`]>
                  <v-btn
                    class="ma-2"
                    :loading="loading2"
                    :disabled="loading2"
                    color="success"
                    @click="loader = 'loading2'"
                  >Pend 
                    <template v-slot:loader>
                     <span>Pending</span>
                    </template>
                  </v-btn>
                </template>
                  <template v-slot:[`item.image`]="{ item }">
                    <img
                      :src="item" >
                  </template>
                  <template v-slot:[`item.actions`]="{ item }">
                    <v-btn
                      small
                      color="cyan"
                      class="mr-1">
                      <v-icon
                        small
                        @click="editItem(item)"
                        color="white">mdi-pencil
                      </v-icon>
                    </v-btn>
                    <v-btn
                      color="error"
                      small>
                      <v-icon
                       small
                       @click="deleteItem(item)"
                        outlined>mdi-delete
                      </v-icon>
                    </v-btn>
                  </template>
              </v-data-table>  
            </v-card>
          </v-card>
        </v-card-container>
      </v-card>
    </v-row>
 </v-main>
</template>
<script>

import Order from '../../apis/Order';
import User from '../../apis/User';
import moment from "moment";
export default {

data: () => ({
 search: '',
 itemsPerPage: 10,
 dialog: false,
 dialogDelete: false,
 dialogShow: false,
 success: false,
 error: false,
  warning: false,
 info:false,
 itemstypes:['ATM','CDM','CRM','DC 365'],
 itemscategories:['Gold','Bronze','Platinum','Silver',],
 models:['DN 200V','DN 100D','DC 365','DC 365LX',],
 headers: [
        
     
        {text: 'Order By', value: 'username',class: " white--text" },
        { text: 'Machine Type', value: 'atm_type', class: " white--text" },
        { text: 'Machine Model', value: 'atm_model', class: " white--text" },
        { text: 'Machine Category', value: 'category', class: " white--text" },
        { text: 'Amount', value: 'amount', class: " white--text" },
        { text: 'Order Date', value: 'created_at',class: " white--text"  },
        { text: 'Arrival Time', value: 'arrival_date',class: " white--text"  },
        { text: 'Warehouse', value: 'warehouse',class: " white--text"  },
        { text: 'Status', value: 'status',class: " white--text"  },
        { text: 'Actions', value: 'actions', sortable: false , class: " white--text" },
      ],

  orders:[],
  user:{},
 editedIndex: -1,
 editedItem: {
    atm_type:'',
    atm_model:'',
    amount:'',
    category:'',
    user_id:'',
 },
 defaultItem: {
   no: 0,
   name: '',
   description: '',
   image: '',
 },
}),

computed: {
 formTitle () {
   return this.editedIndex === -1 ? 'Complete Information to order' : 'Edit order ATM Information'
 },
},

watch: {
 dialog (val) {
   val || this.close()
 },
 dialogShow(val) {
   val || this.closeShow()
 },
 dialogDelete (val) {
   val || this.closeDelete()
 },
},

created () {
//  this.initialize()
this.getOrder()
},

methods: {
  hide_success: function (event) {
      console.log('Hide')
      // `event` is the native DOM event
      window.setInterval(() => {
        this.success = false;
        this.error = false;
        this.info=false;
        this.warning = false;
        
      }, 5000)    
    },
      formatDate(value) {
      return moment(value).format("DD-MM-YYYY")
  },
  getOrder(){
    User.auth()
    .then(Response=>{
      const i = Response.data.id;
        console.log(i)
        Order.show(i)
        .then(Response=>{
          this.orders= Response.data;
        console.log(Response.data);
    })
      .catch(error => {
          if (error.response) {
            this.errors = error.response.data.errors;
          
          } 
            console.log(error.Response.data.errors); 
      })
    })
    
  },
 editItem (item) {
   this.editedIndex = this.orders.indexOf(item)
   this.editedItem = Object.assign({}, item)
   this.dialog = true
   console.log(item)
 },

 showItem (item) {
   this.editedIndex = this.orders.indexOf(item)
   this.editedItem = Object.assign({}, item)
   this.dialogShow = true
 },

 deleteItem (item) {
   this.editedIndex = this.orders.indexOf(item)
   this.editedItem = Object.assign({}, item)
   this.dialogDelete = true
 },

 deleteItemConfirm () {
  const index = this.editedIndex;
  Order.deleteOrder(this.editedItem.id)
   .then(res=>{
    console.log(res.data);
    if(res.data.status==200){
        this.info=true;
      this.orders.data.splice(index, 1)
    }
    else if(res.data.status==300){
        this.warning=true
    }
  })
  .catch(err =>{
    console.log(err.response)
    this.warning=true;
    this.close()
    
  });
  this.getOrder();
  this.closeDelete()
 },

 close () {
   this.dialog = false
   this.$nextTick(() => {
     this.editedItem = Object.assign({}, this.defaultItem)
     this.editedIndex = -1
   })
 },
 closeShow () {
   this.dialogShow = false
   this.$nextTick(() => {
     this.editedItem = Object.assign({}, this.defaultItem)
     this.editedIndex = -1
   })
 },
 closeDelete () {
   this.dialogDelete = false
   this.$nextTick(() => {
     this.editedItem = Object.assign({}, this.defaultItem)
     this.editedIndex = -1
   })
 },

 save (item) {
    console.log("data", item.id);
   if (this.editedIndex > -1) {
     const index = this.editedIndex
    console.log("id", this.editedItem.id);
    Order.update(this.editedItem.id,this.editedItem)
    .then(response=>{
      this.info= true;
      Object.assign(this.users[index],response.data)
      console.log("data", response.data);
      this.close();
    })
    .catch(error => {
      console.log(error.response)
    })
     
   } else {
    let data = {};
    this.editedItem.user_id = item.id,
    data = this.editedItem
    Order.create(data)
     .then(res => {
      console.log(res);
      this.success=true
       this.close();
     })
     .catch(err => {
      console.dir(err)
      this.error=true;
     })
   }
   this.getOrder();
  this.close()
 },
},
mounted: function () {
    if(alert){
      this.hide_success();
    }
    User.auth().then(response => {
      this.user = response.data;
      console.log(response.data);
    });
   }
}
</script>
<style scoped>
::v-deep .v-data-table-header {
background-color:#35acd3;

}
v-data-table >>> div > table {
border-spacing: 0 0.5rem;

}
::v-deep .v-alert {
  width: 300px;
  margin-top: 20px;
  position: fixed;
  right: 0%;
  z-index: 1;
  top: 50px;
  
}
</style>
