<script>
import Using from "../../apis/Using";
import Order from "../../apis/Order";
import BarChart from "@/components/BarChart.vue";
export default {
  components: {
    BarChart,
  },
  data: () => ({
    total: [],
    orders: [],
    itemsPerPage: 10,
    headers: [
      { text: "Type", value: "atm_type", class: " white--text" },
      { text: "Model", value: "atm_model", class: " white--text" },
      { text: "Amount", value: "amount", class: " white--text" },
      { text: 'Status', value: 'status',class: " white--text"  },
    ],
  }),
  created() {
    this.getOrder();
  },
  methods: {
    getOrder() {
      Order.list().then((Response) => {
        this.orders = Response.data;
        console.log(this.order);
      });
    },
  },
  mounted: function () {
    Using.getTotal().then((Response) => {
      this.total = Response.data;
      console.log(this.total);
    });
  },
};
</script>
